.button-wrapper {
  height: 1.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6em;
  background-color: #808e9b;
  color: #222f3e;
  flex: 1;
  border: 4px solid #222f3e;
  margin: 10px;
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 4px;
  font-family: 'Electrolize', sans-serif;
  cursor: pointer;
}

.operator {
  background-color: #dfe6e9;
}

.button-wrapper:hover {
  background-color: #60a3bc;
}